$Montserrat:('Montserrat','Bold',700),
            ('Montserrat','Regular',400),
            ('fontawesome','duotone', 900),
            ('fontawesome','light', 300),
            ;

@each $family, $name, $weight in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}



$main:#F1F1F1;
$white: white;
$black: black;
$card: #E1E1E1;
$black__font: rgba(50, 50, 50, 1);
$grey : #C4C4C4;
$grey__font: rgba(225, 225, 225, 1);
$shadow : linear-gradient(180deg, rgba(50, 50, 50, 0.75) 0%, rgba(50, 50, 50, 0.5) 100%);
