.blog__title {
    text-align: center;
    margin-top: 70px;
    margin-bottom: 100px;
    font-weight: 400;
    font-size: 22px;
}

.blog {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 116px;
    gap: 30px;

    @include media($md) {
        grid-template-columns: repeat(1, 1fr);

    }
}

.news {
    position: relative;

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__content {
        position: relative;
        overflow: hidden;
        background: $shadow;
        padding: 20px;
    }

    &__advice {
        padding: 15px 30px;
        background: $white;
        color: $black__font;
        border-radius: 8px;
        display: inline-block;
        margin-bottom: 40px;
    }

    &__title {
        color: $white;
        font-weight: 700;
        font-size: 22px;
        margin-bottom: 20px;
    }

    &__text {
        font-size: 18px;
        color: $grey__font;
        margin-bottom: 40px;
    }
    &__time {
        font-size: 16px;
        color: $grey__font;
    }

    &__small-block {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 30px;
        
        @include media($xs) {
            grid-template-columns: repeat(1, 1fr);
        }
        
    }

    

}

.news__small {
    .news {

        &__advice {
            padding: 10px 15px;
            
        }
        &__title {
            @include limit(2);
            margin-bottom: 13px;
        }

        &__advice {
            margin-bottom: 20px;
        }

        &__text {
            margin-bottom: 20px;
        }
    



        &__text {
            @include limit(2);
        }
    }
}

.left {
    .news__small-block {
        margin-top: 30px;
    }
}

.right {
    .news__small-block {
        margin-bottom: 30px;
    }
}