@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-duotone.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  font-family: 'Montserrat';
  background: #F1F1F1;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::after, ::before {
  font-family: 'fontawesome';
}

.icon-search::before {
  content: "\f002";
}

.icon-heart::after {
  content: "\f004";
  color: black;
}

.icon-basket::after {
  content: "\f07a";
  color: black;
}

.icon-profile::after {
  content: "\f007";
}

.icon-burger::after {
  content: "\f142";
  font-size: 40px;
  color: black;
  display: none;
}

.icon-clock::after {
  content: '\f017';
  margin-right: 10px;
}

.icon-vk::after {
  content: "\f189";
}

.icon-youtube::after {
  content: "\f167";
}

.icon-insta::after {
  content: "\f16d";
}

.icon-facebook::after {
  content: "\f39e";
}

.panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 0;
  border-bottom: 1px solid #C4C4C4;
}

.panel a {
  color: #323232;
}

.panel-language {
  text-transform: uppercase;
  font-size: 18px;
}

nav {
  position: sticky;
  top: 0;
  z-index: 999;
  transition: 0.5s;
}

@media (max-width: 992px) {
  nav {
    background: #f2f2f2;
  }
}

nav:hover {
  background: #F1F1F1;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
  border-bottom: 1px solid #C4C4C4;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 992px) {
  .nav {
    padding: 0;
  }
}

.menu {
  display: flex;
}

.menu a {
  color: #323232;
  margin-right: 50px;
}

@media (max-width: 992px) {
  .menu a {
    display: block;
    color: white;
    margin-bottom: 15px;
  }
}

.menu.active {
  display: block;
}

@media (max-width: 992px) {
  .menu {
    display: none;
    transition: 0.5s;
    position: absolute;
    flex-direction: column;
    margin: 0;
    top: 60px;
    background: #323232;
    padding: 10px;
    max-width: 120px;
  }
}

.logo {
  font-weight: 700;
  font-size: 32px;
  line-height: 23px;
  color: black;
}

.catalog {
  display: flex;
}

.catalog a {
  color: black;
  margin-left: 30px;
}

.icon-burger {
  width: 30px;
}

@media (max-width: 992px) {
  .icon-burger::after {
    display: flex;
    justify-content: center;
    position: relative;
  }
}

.products {
  margin-top: 40px;
}

.card {
  background: #E1E1E1;
  border-radius: 8px;
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
  align-items: center;
}

.card .icon-heart {
  padding: 16px;
  background: white;
  color: black;
  border-radius: 8px;
  position: absolute;
  right: 27px;
  top: 20px;
}

@media (max-width: 576px) {
  .card .icon-heart {
    right: 5px;
    top: 5px;
  }
}

.card .icon-basket {
  padding: 15px;
  background: white;
  color: black;
  border-radius: 8px;
  position: absolute;
  right: 27px;
  top: 85px;
}

@media (max-width: 576px) {
  .card .icon-basket {
    right: 5px;
    top: 60px;
  }
}

.card-sale {
  background: #323232;
  position: absolute;
  left: 15px;
  top: 20px;
  border-radius: 8px;
  padding: 10px 30px;
  color: #C4C4C4;
  font-size: 20px;
}

@media (max-width: 576px) {
  .card-sale {
    left: 5px;
    top: 5px;
  }
}

.card-info {
  text-align: center;
}

.card-title {
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 15px;
  margin-top: 30px;
}

.card-price {
  font-size: 18px;
}

@media (max-width: 576px) {
  .card {
    padding: 20px;
  }
}

.big {
  width: 66.66667%;
  padding-left: 15px;
  padding-right: 15px;
}

.big .card {
  flex-direction: row;
  display: flex;
  height: 100%;
}

@media (max-width: 992px) {
  .big .card {
    padding: 40px 0;
  }
}

@media (max-width: 576px) {
  .big .card {
    flex-direction: column;
  }
}

@media (max-width: 576px) {
  .big .card-img {
    max-width: 150px;
  }
}

.big .card-title {
  margin-top: 0;
}

.big .card-info {
  margin-left: 40px;
}

@media (max-width: 576px) {
  .big .card-info {
    margin: 0;
  }
}

@media (max-width: 992px) {
  .big {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 30px;
  }
}

.small {
  width: 33.33333%;
  padding-left: 15px;
  padding-right: 15px;
}

.small .card-img {
  max-width: 125px;
}

@media (max-width: 576px) {
  .small .card-img {
    max-width: 80px;
  }
}

@media (max-width: 992px) {
  .small .card {
    width: 41.66667%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 768px) {
  .small .card-sale {
    padding: 5px;
  }
}

@media (max-width: 576px) {
  .small .card-title {
    font-size: 12px;
  }
  .small .card-price {
    font-size: 10px;
  }
}

@media (max-width: 768px) {
  .small .card .icon-basket, .small .card .icon-heart {
    padding: 5px;
    border: 1px solid #323232;
  }
}

.small .card:first-of-type {
  margin-bottom: 30px;
}

@media (max-width: 992px) {
  .small .card:first-of-type {
    margin: 0;
  }
}

@media (max-width: 992px) {
  .small {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    align-items: start;
    justify-content: space-evenly;
  }
}

.all-title {
  text-align: center;
  font-weight: 400;
  font-size: 22px;
  margin: 100px 0;
}

.all-item {
  width: 25%;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 30px;
}

@media (max-width: 1140px) {
  .all-item {
    width: 33.33333%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 992px) {
  .all-item {
    width: 50%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 576px) {
  .all-item {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.all .card-img {
  max-width: 96px;
}

.blog__title {
  text-align: center;
  margin-top: 70px;
  margin-bottom: 100px;
  font-weight: 400;
  font-size: 22px;
}

.blog {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 116px;
  gap: 30px;
}

@media (max-width: 992px) {
  .blog {
    grid-template-columns: repeat(1, 1fr);
  }
}

.news {
  position: relative;
}

.news__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.news__content {
  position: relative;
  overflow: hidden;
  background: linear-gradient(180deg, rgba(50, 50, 50, 0.75) 0%, rgba(50, 50, 50, 0.5) 100%);
  padding: 20px;
}

.news__advice {
  padding: 15px 30px;
  background: white;
  color: #323232;
  border-radius: 8px;
  display: inline-block;
  margin-bottom: 40px;
}

.news__title {
  color: white;
  font-weight: 700;
  font-size: 22px;
  margin-bottom: 20px;
}

.news__text {
  font-size: 18px;
  color: #e1e1e1;
  margin-bottom: 40px;
}

.news__time {
  font-size: 16px;
  color: #e1e1e1;
}

.news__small-block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}

@media (max-width: 576px) {
  .news__small-block {
    grid-template-columns: repeat(1, 1fr);
  }
}

.news__small .news__advice {
  padding: 10px 15px;
}

.news__small .news__title {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 13px;
}

.news__small .news__advice {
  margin-bottom: 20px;
}

.news__small .news__text {
  margin-bottom: 20px;
}

.news__small .news__text {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.left .news__small-block {
  margin-top: 30px;
}

.right .news__small-block {
  margin-bottom: 30px;
}

.footer {
  background: white;
}

@media (max-width: 768px) {
  .footer {
    text-align: center;
  }
}

.footer li {
  margin-bottom: 10px;
}

.footer a {
  color: black;
  font-size: 18px;
  line-height: 13px;
}

.footer__link {
  font-weight: 400;
  font-size: 18px;
  margin-bottom: 40px;
  line-height: 20px;
}

.footer__menu {
  padding: 65px 0;
  display: grid;
  grid-template-columns: repeat(1, 1.5fr 2fr 3fr);
}

@media (max-width: 768px) {
  .footer__menu {
    grid-template-columns: repeat(1, 1fr);
  }
}

.address__title {
  margin-bottom: 20px;
}

.phone-number:first-of-type {
  margin-right: 20px;
}
