.panel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 18px 0;
    border-bottom: 1px solid $grey;

    a {
        color: $black__font;
    }
    &-language {
        text-transform: uppercase;
        font-size: 18px;
    }



    // &::after {
    //     content: '';
    //     display: block;
    //     height: 1px;
    //     width: 100%;
    //     background: $grey;
    // }


}

nav {
    position: sticky;
    top: 0;
    z-index: 999;
    transition: 0.5s;

    @include media($md) {
        background: hsl(0, 0%, 95%);

    }

    &:hover {
        background: $main;
    }
}

.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0;
    border-bottom: 1px solid $grey;
    @include col(12);
    @include media($md) {
        padding: 0;
    }
}

.menu{
    display: flex;

    a {
        color: $black__font;
        margin-right: 50px;
        
        @include media($md) {
            display: block;
            color: $white;
            margin-bottom: 15px;
        }
    }
    
    &.active {
    display: block;
}

    @include media ($md){
        display: none;
        transition: 0.5s;
        position: absolute;
        flex-direction: column;
        margin: 0;
        top: 60px;
        background: $black__font;
        padding: 10px;
        max-width: 120px;
        
    }
}

.logo {
    font-weight: 700;
    font-size: 32px;
    line-height: 23px;
    color: $black;
}


.catalog {
    display: flex;
    a {
        color: $black;
        margin-left: 30px;
    }
}

.icon-burger{
    width: 30px;

    &::after {
    @include media($md) {
        display: flex;
        justify-content: center;
        position: relative;
    }
}
}
