*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a{
    text-decoration: none;
}

ul {
    list-style: none;
}

body {
    font-family: 'Montserrat';
    background: $main;
}

.container {
    @include container();
}

.row {
    @include row();
}

::after, ::before {
    font-family: 'fontawesome';
}



.icon {
    &-search::before {
        content: "\f002";
    }

    &-heart::after {
        content: "\f004";
        color: $black;
    }

    &-basket::after {
        content: "\f07a";
        color: $black;
    }

    &-profile::after {
        content: "\f007";
    }
    &-burger::after {
        content: "\f142";
        font-size: 40px;
        color: $black;
        display: none;
    }
    &-clock::after {
        content: '\f017';
        margin-right: 10px;
    }
    &-vk::after {
        content: "\f189";
    }
    &-youtube::after {
        content: "\f167";
    }
    &-insta::after {
        content: "\f16d";
    }
    &-facebook::after {
        content: "\f39e";
    }

}


