.products {
    margin-top: 40px;
}

.card {
    background: $card;
    border-radius: 8px;
    padding: 20px 15px;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: center;
    align-items: center;

    .icon-heart {
        padding: 16px;
        background: $white;
        color: $black;
        border-radius: 8px;
        position: absolute;
        right: 27px;
        top: 20px;
        @include media($xs) {
            right: 5px;
            top: 5px;
        }
    }

    .icon-basket {
        padding: 15px;
        background: $white;
        color: $black;
        border-radius: 8px;
        position: absolute;
        right: 27px;
        top: 85px;
        @include media($sm) {   
        }
        @include media($xs) {
            right: 5px;
            top: 60px;
        }
    }

    

    &-sale {
        background: $black__font;
        position: absolute;
        left: 15px;
        top: 20px;
        border-radius: 8px;
        padding: 10px 30px;
        color: $grey;
        font-size: 20px;
        @include media($xs) {
            left: 5px;
            top: 5px;
        }
    }
    &-info {
        text-align: center;
    }
    &-title {
        font-weight: 700;
        font-size: 20px;
        margin-bottom: 15px;
        margin-top: 30px;
    }
    &-price {
        font-size: 18px;
    }

   
    @include media($xs) {
        padding: 20px;
    }
    
}


.big {
    @include col(8);
    .card {
        flex-direction: row;
        display: flex;
        height: 100%;
        @include media($md) {
            padding: 40px 0;

        }
        @include media($xs) {
            flex-direction: column;
        }
        &-img {
            @include media($xs) {
                max-width: 150px;
            }
        }
        &-title {
            margin-top: 0;
        }
    }


    .card-info {
        margin-left: 40px;
        @include media($xs) {
            margin: 0;
        }
        
    }
    @include media($md) {
        @include col(12);
        margin-bottom: 30px;
    }
    
}

.small {
    @include col(4);
    .card-img {
        max-width: 125px;
        
        @include media($xs) {
            max-width: 80px;
        }
    }

    

    .card {
        @include media($md) {
            @include col(5)
        }

        @include media($sm) {
            &-sale {
                padding: 5px;
            }
        }
        @include media($xs) {
            &-title {
                font-size: 12px;
            }
            &-price {
                font-size: 10px;
            }
        }

        .icon-basket, .icon-heart {
            @include media($sm) {
                padding: 5px;
                border: 1px solid $black__font;
            }
            
        }
        

    }

    .card:first-of-type {
        margin-bottom: 30px;
        @include media($md) {
            margin: 0;
        }
    }

    @include media($md) {
        @include col(12);
        display: flex;
        align-items: start;
        justify-content: space-evenly;
    }   
}



.all-title {
    text-align: center;
    font-weight: 400;
    font-size: 22px;
    margin: 100px 0;
}

.all {
    &-item{
        @include col(3);
        margin-bottom: 30px;
        
        @include media($lg) {
            @include col(4)
        }
        @include media($md) {
            @include col(6)
        }
        @include media($xs) {
            @include col(12)
        }
    }
    .card {
        &-img {
            max-width: 96px;
        }
            


    }
}